import React, { useContext } from 'react';
import { View, Text, StyleSheet, TouchableOpacity, Button } from 'react-native';
import UserContext from '../context/UserContext';

const MenuScreen = ({ navigation }) => {
    const { logout } = useContext(UserContext);
    const screens = [
        { name: 'Manage Product', route: 'Manage Product' },
        { name: 'Manage Orders', route: 'Manage Orders' },
        { name: 'Manage User', route: 'Manage User' },
        { name: 'Register User', route: 'Register User' },
    ];

    const handleLogout = async () => {
        try {
            logout()
            // await AsyncStorage.removeItem('userCredentials');
            navigation.navigate('Login'); // Assuming 'Login' is the name of your login screen
        } catch (error) {
            console.error('Error clearing user credentials', error);
        }
    };

    return (
        <View style={styles.container}>
            {screens.map((screen, index) => (
                <TouchableOpacity
                    key={index}
                    style={styles.card}
                    onPress={() => navigation.navigate(screen.route)}
                >
                    <Text style={styles.text}>{screen.name}</Text>
                </TouchableOpacity>
            ))}
            <Button title="Logout" onPress={handleLogout} style={styles.logoutButton} />

        </View>
    );
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
        alignItems: 'center',
        justifyContent: 'center',
    },
    card: {
        backgroundColor: '#e0e0e0',
        padding: 20,
        marginVertical: 10,
        width: '80%',
        alignItems: 'center',
    },
    text: {
        fontSize: 18,
    },
    logoutButton: {
        marginTop: 20,
        backgroundColor: '#ff4444', // Example: red color for logout
        color: 'white',
        padding: 10,
        borderRadius: 10,
        width: '80%',
        alignSelf: 'center',
        // More styling as needed
    },
});

export default MenuScreen;
